body {
  margin: 0; }

.container {
  margin: 0 auto;
  max-width: 1000px; }

.header {
  margin: 0 auto; }

.header__top {
  max-width: 940px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
  background: #ffffff; }

.header__bottom {
  padding-left: 25px;
  padding-right: 25px;
  background: #1f1f1f; }

.header__bottom-center {
  max-width: 950px;
  margin: 0 auto;
  display: flex;
  padding-top: 10px; }

.socials {
  margin-right: 12%; }

.socials__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  width: 130px; }

.socials__item {
  width: 32px;
  height: 31px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #c2c2c2;
  border-bottom: 1px solid #c2c2c2;
  border-left: 1px solid #c2c2c2; }

.socials__item:last-of-type {
  border-right: 1px solid #c2c2c2; }

.socials__link {
  font-size: 0;
  width: 100%;
  height: 100%; }

.socials__link:hover {
  opacity: 0.7; }

.socials__link:focus {
  opacity: 0.7; }

.socials__link--fb {
  background: url("../img/icon-facebook.png") 50% 50% no-repeat; }

.socials__link--vk {
  background: url("../img/icon-vk.png") 50% 50% no-repeat; }

.socials__link--tw {
  background: url("../img/icon-twitter.png") 50% 50% no-repeat; }

.socials__link--g {
  background: url("../img/icon-google.png") 50% 50% no-repeat; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.city {
  display: flex;
  align-items: center;
  padding-left: 20px;
  margin-top: 8px;
  margin-right: 13%; }

.city__name {
  font-family: "Proxima Nova Condensed";
  font-size: 14px;
  line-height: 18px;
  font-weight: 600;
  margin-right: 12px;
  position: relative;
  color: #1f1f1f;
  cursor: pointer; }

.city__name::before {
  content: "";
  position: absolute;
  width: 11px;
  height: 16px;
  top: 0px;
  left: -20px;
  background: url("../img/icon-geo.png") 50% 50% no-repeat; }

.city__name {
  border-bottom: 1px solid #c7c7c7; }

.city__name:hover {
  opacity: 0.7; }

.city__name:focus {
  opacity: 0.7; }

.city__button {
  width: 10px;
  height: 6px;
  cursor: pointer;
  background: url("../img/icon-corner.png") 50% 50% no-repeat; }

.city__button:hover {
  opacity: 0.7; }

.city__button:focus {
  opacity: 0.7; }

.phone {
  margin-top: 8px;
  margin-right: 11.5%; }

.phone__link {
  font-family: "Proxima Nova Condensed";
  font-size: 17px;
  line-height: 18px;
  font-weight: 600;
  padding-left: 20px;
  margin-right: 10px;
  color: #1f1f1f;
  text-decoration: none;
  position: relative; }

.phone__link:hover {
  opacity: 0.7; }

.phone__link:focus {
  opacity: 0.7; }

.phone__link::before {
  content: "";
  position: absolute;
  width: 13px;
  height: 15px;
  top: 2px;
  left: 0;
  background: url("../img/icon-phone.png") 50% 50% no-repeat; }

.user-block {
  display: flex;
  margin-top: 8px; }

.user-block__login {
  padding-left: 25px;
  margin-right: 65px; }

.user-block__login-link {
  font-family: "Proxima Nova Condensed";
  font-size: 15px;
  line-height: 18px;
  font-weight: 600;
  color: #1f1f1f;
  text-decoration: none;
  border-bottom: 1px dashed #1f1f1f;
  position: relative; }

.user-block__login-link::before {
  position: absolute;
  content: "";
  width: 14px;
  height: 15px;
  top: 3px;
  left: -25px;
  background: url("../img/icon-login.png") 50% 50% no-repeat; }

.user-block__login-link:hover {
  opacity: 0.7; }

.user-block__login-link:focus {
  opacity: 0.7; }

.user-block__cart {
  padding-left: 25px;
  display: flex;
  align-items: flex-start; }

.user-block__cart-link {
  font-family: "Proxima Nova Condensed";
  font-size: 15px;
  line-height: 18px;
  font-weight: 600;
  color: #1f1f1f;
  text-decoration: none;
  border-bottom: 1px dashed #1f1f1f;
  position: relative;
  margin-right: 15px; }

.user-block__cart-link::before {
  position: absolute;
  content: "";
  width: 14px;
  height: 15px;
  top: 3px;
  left: -25px;
  background: url("../img/icon-cart.png") 50% 50% no-repeat; }

.user-block__cart-link:hover {
  opacity: 0.7; }

.user-block__cart-link:focus {
  opacity: 0.7; }

.user-block__cart-counter {
  font-family: "PT Serif Italic";
  font-size: 14px;
  line-height: 18px;
  font-weight: 300;
  color: #b98348;
  padding-left: 5px;
  border-left: 1px solid #b98348; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap; }

.nav__item {
  margin-right: 52px;
  margin-top: 8px; }

.nav__link {
  font-family: "Proxima Nova Condensed";
  font-size: 15px;
  line-height: 18px;
  font-weight: 600;
  color: white;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  text-decoration: none; }

.nav__link:hover {
  opacity: 0.7; }

.nav__link:focus {
  opacity: 0.7; }

.nav__link--active {
  border-bottom: 1px dashed #ffffff; }

.logo {
  margin-right: 95px;
  margin-bottom: 10px; }

.logo__image {
  display: block;
  width: 157px;
  height: 38px; }

.banner {
  background: url("../img/image-big-man.jpg") 50% 100% no-repeat, url("../img/image-big.jpg") 50% 100% repeat, #c9a57a;
  margin: 0 auto;
  padding-top: 138px;
  padding-bottom: 145px;
  display: flex;
  justify-content: center; }

.banner__inner {
  margin-left: -29%;
  width: 480px;
  text-align: center;
  position: relative; }

.banner__inner::before {
  content: "";
  position: absolute;
  top: -28px;
  left: 50%;
  margin-left: -80px;
  width: 160px;
  height: 3px;
  background: #ffffff; }

.banner__inner::after {
  content: "";
  position: absolute;
  bottom: -42px;
  left: 50%;
  margin-left: -80px;
  width: 160px;
  height: 3px;
  background: #ffffff; }

.banner__text {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Proxima Nova";
  font-size: 48px;
  line-height: 54px;
  font-weight: 700;
  color: white; }

.banner__text:first-of-type {
  font-weight: 400; }

.categories {
  padding-top: 34px;
  padding-bottom: 30px; }

.categories__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  flex-shrink: 0;
  flex-grow: 0; }

.categories__item {
  margin-right: 8px; }

.categories__item:last-of-type {
  margin-right: 0; }

.categories__link {
  font-family: "Proxima Nova";
  font-size: 14px;
  line-height: 44px;
  font-weight: 700;
  color: black;
  text-transform: uppercase;
  text-decoration: none;
  display: block;
  padding-left: 18px;
  padding-right: 18px; }

.categories__link--active {
  outline: 2px solid #b98348;
  color: #b98348; }

.categories__link:hover {
  outline: 2px solid #b98348;
  color: #b98348; }

.categories__link:focus {
  outline: 2px solid #b98348;
  color: #b98348; }

.articles__list {
  background: red;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0; }
