.socials {
  margin-right: 12%;
}

.socials__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  width: 130px;
}

.socials__item {
  width: 32px;
  height: 31px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #c2c2c2;
  border-bottom: 1px solid #c2c2c2;
  border-left: 1px solid #c2c2c2;
}

.socials__item:last-of-type {
  border-right: 1px solid #c2c2c2;
}

.socials__link {
  font-size: 0;
  width: 100%;
  height: 100%;
}

.socials__link:hover {
  opacity: 0.7;
}

.socials__link:focus {
  opacity: 0.7;
}

.socials__link--fb {
  background: url("../img/icon-facebook.png") 50% 50% no-repeat;
}

.socials__link--vk {
  background: url("../img/icon-vk.png") 50% 50% no-repeat;
}

.socials__link--tw {
  background: url("../img/icon-twitter.png") 50% 50% no-repeat;
}

.socials__link--g {
  background: url("../img/icon-google.png") 50% 50% no-repeat;
}
